
















































































import uniSegmentedControl from '../../components/uni-segmented-control.vue';
import uniNavBar from '../../components/uni-nav-bar.vue';
import uniIcon from '../../components/uni-icon.vue';
import uniLoadMore from '@/components/uni-load-more.vue';
import {
	mapState,
	mapMutations
} from 'vuex';


export default {
	computed: mapState([ 'hasLogin','userInfo']),
	components: {
		uniNavBar,
		uniIcon,
		uniLoadMore,
		uniSegmentedControl
	},
	onLoad:function(e){
		
			uni.request({
				url:`${this.$serverUrl}/index/index?cid=`+e.cid,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				method:"GET",
				success:(res)=>{
					//const data =  JSON.parse(res.data);
					if(this.newsitems.length<=10){
						this.loadingType = 2;
					}
					this.cid=e.cid,
					this.newsitems = res.data.data;
				}
			})
		
		
	},
	
	data() {
		return {
			isup1:1,
			isup2:3,
			isup3:5,
			isclick:0,
			isscroll: false,
			isactive: 0,
			indicatorDots: true,
			autoplay: true,
			interval: 5000,
			duration: 500,
			loadingType: 0,
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			isPosition:false,
			activeClass:'tabactive',
			falseClass:'tabactive-nop',	
			newsitems:[],
			cid:0,
			limit:2,
			request:true,
		};
	},
	
	methods:{
		addData(){
			uni.request({
				url: 'http://t2.qingxinedu.cn/api/index/index',
				method: 'GET',
				data: {},
				success: (res) => {
					//console.log(res.data);
					const loadData = res.data;
					this.newsitems =this.newsitems.concat(loadData.data) 
				    }
			    });
				const maxLength = this.newsitems.length
				
				if (this.newsitems.length >maxLength) {
					this.loadingType=2;
					return;
				}
		},
		LoadMore(){
			
		
			if(this.loadingType !==2){
				this.loadingType = 1;
				if(this.request){
					this.request=false;
					setTimeout(() => {
						this.addData();
					}, 1200);
				}
			}	
		},
		//分类控制
		activefn(n,isup=1) {
			this.isactive = n
			if(isup==1||isup==-1){
				this.isup1=-isup
				this.sort(this.cid,this.isup1)
			}else if(isup==2||isup==-2){
				this.isup2=-isup
				this.sort(this.cid,this.isup2)
			}else if(isup==3||isup==-3){
				this.isup3=-isup
				this.sort(this.cid,this.isup3)
			}
		},
		sort(cid,sort_id){
			uni.request({
				url:"http://t2.qingxinedu.cn/api/index/index?cid="+cid+'&order='+sort_id,
				method:"GET",
				success: res=>{
					this.newsitems = res.data.data;
				}
			})
		},
		scro(e){	
			console.log(111)
			if(e.detail.scrollTop > 250){
				this.isPosition = true;
			}
		},
		top(){
			this.isPosition =false;
		},
		
		back(){
			uni.reLaunch({
				url:"../index/index"
			});
		}
	},
	
}
